//Import react
import React from 'react';

import {AppHeader} from './AppHeader.js';

export class App extends React.Component{
  constructor(props){
    //Call parent
    super(props);

    window.Host = window.location.hostname;
    window.Port = 8080;
  }

  render(){
    //Render page
    if(window.location.pathname == "/Info"){
      return(
        <div>
          <AppHeader/>
          Info
        </div>
      )
    }

    if(window.location.pathname == "/Directory"){
      return(
        <div>
          <AppHeader/>
          Directory
        </div>
      )
    }

    if(window.location.pathname == "/Opportunites"){
      return(
        <div>
          <AppHeader/>
          Opportunites
        </div>
      )
    }

    else{
      return(
        <div>
          <AppHeader/>
          Info
        </div>
      )
    }

  }

}
